"use client";
import { useState, useEffect, useMemo, useRef } from "react";
import { Button } from "@/components/ui/button";
import { supabase } from "@/lib/supabaseClient";
import { useRouter } from "next/navigation";
import type { Factory } from "@/lib/factories";

// factories Îç∞Ïù¥ÌÑ∞ÏóêÏÑú ÏòµÏÖò Ï∂îÏ∂ú Ïú†Ìã∏(Í≥µÏû• Ï∞æÍ∏∞ÏóêÏÑú Î≥µÏÇ¨)
const moqRanges = [
  { label: "0-50", min: 0, max: 50 },
  { label: "51-100", min: 51, max: 100 },
  { label: "101-300", min: 101, max: 300 },
  { label: "301+", min: 301, max: Infinity },
];

// Ï±ÑÌåÖ ÎßêÌíçÏÑ† Ïª¥Ìè¨ÎÑåÌä∏ (fade-in + ÌÉÄÏù¥Ìïë Ìö®Í≥º)
function ChatBubble({ text, type, isTyping, showCursor, onEdit }: { text: string; type: "question" | "answer"; isTyping?: boolean; showCursor?: boolean; onEdit?: () => void }) {
  return (
    <div className={`flex flex-col items-${type === "answer" ? "end" : "start"} w-full`}>
      <div className={`relative px-4 py-2 rounded-2xl text-base animate-fade-in max-w-[80%] ${type === "question" ? "bg-white text-black self-start" : "bg-[#222222] text-white self-end"}`} style={{ minHeight: 40 }}>
        {isTyping ? (
          <span>
            {text}
            {showCursor && <span className="inline-block w-2 animate-blink ml-0.5">|</span>}
          </span>
        ) : (
          text
        )}
      </div>
      {/* ÎãµÎ≥Ä(ÏÑ†ÌÉùÏßÄ) ÎßêÌíçÏÑ†ÏóêÎßå ÏàòÏ†ï Î≤ÑÌäº ÎÖ∏Ï∂ú */}
      {type === "answer" && onEdit && (
        <button
          className="mt-1 text-xs text-gray-400 underline hover:text-[#222222]"
          onClick={onEdit}
        >
          ÏàòÏ†ï
        </button>
      )}
    </div>
  );
}

export default function MatchingPage() {
  // Í≥µÏû• Îç∞Ïù¥ÌÑ∞ state
  const [factories, setFactories] = useState<Factory[]>([]);

  useEffect(() => {
    async function fetchFactories() {
      const { data } = await supabase.from("donggori").select("*");
      setFactories(data ?? []);
    }
    fetchFactories();
  }, []);

  // factories state Í∏∞Î∞ò ÏòµÏÖò Ï∂îÏ∂ú Ìï®Ïàò (Ìï®Ïàò ÎÇ¥Î∂ÄÏóêÏÑú ÏÑ†Ïñ∏)
  function getOptions(key: string): string[] {
    if (key === 'admin_district') {
      return Array.from(new Set(factories.map((f: Factory) => f.admin_district).filter((v: string | undefined): v is string => typeof v === 'string' && Boolean(v))));
    }
    if (key === 'processes') {
      return Array.from(new Set(factories.flatMap((f: Factory) => f.processes ? String(f.processes).split(',').map((v: string) => v.trim()) : []).filter((v: string) => typeof v === 'string' && Boolean(v))));
    }
    if (key === 'sewing_machines' || key === 'pattern_machines' || key === 'special_machines') {
      return Array.from(new Set(factories.flatMap((f: Factory) => f[key] ? String(f[key]).split(',').map((v: string) => v.trim()) : []).filter((v: string) => typeof v === 'string' && Boolean(v))));
    }
    if (key === 'items') {
      const arr = factories.flatMap((f: Factory) => [
        f.top_items_upper, f.top_items_lower, f.top_items_outer, f.top_items_dress_skirt, f.top_items_bag, f.top_items_fashion_accessory, f.top_items_underwear, f.top_items_sports_leisure, f.top_items_pet
      ].filter((v: string | undefined): v is string => typeof v === 'string' && Boolean(v)));
      return Array.from(new Set(arr.flatMap((i: string) => String(i).split(',').map((v: string) => v.trim())).filter((v: string) => typeof v === 'string' && Boolean(v))));
    }
    return [];
  }

type ScoredFactory = Factory & { score: number };

  // ÎèôÏ†Å ÏßàÎ¨∏/ÏòµÏÖò useMemoÎäî Î∞òÎìúÏãú Ìï®Ïàò ÎÇ¥Î∂ÄÏóêÏÑú Ìò∏Ï∂ú
  const QUESTIONS = useMemo(() => [
    { question: "Ïñ¥Îñ§ Í≥µÏ†ïÏùÑ ÏõêÌïòÏãúÎÇòÏöî?", key: "processes", options: getOptions("processes") },
    { question: "ÏßÄÏó≠ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî", key: "admin_district", options: getOptions("admin_district") },
    { question: "MOQ(ÏµúÏÜå Ï£ºÎ¨∏ ÏàòÎüâ)ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî", key: "moq", options: moqRanges.map(r => r.label) },
    { question: "Ïû¨Î¥âÍ∏∞Î•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî", key: "sewing_machines", options: getOptions("sewing_machines") },
    { question: "Ìå®ÌÑ¥Í∏∞Î•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî", key: "pattern_machines", options: getOptions("pattern_machines") },
    { question: "ÌäπÏàòÍ∏∞Î•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî", key: "special_machines", options: getOptions("special_machines") },
    { question: "Ïñ¥Îñ§ ÌíàÎ™©ÏùÑ ÏõêÌïòÏãúÎÇòÏöî?", key: "items", options: getOptions("items") },
  ], [factories]);
  // Ï±ÑÌåÖ Î©îÏãúÏßÄ(ÏßàÎ¨∏/ÎãµÎ≥Ä ÏàúÏÑúÎåÄÎ°ú)
  const [chat, setChat] = useState<{ type: "question" | "answer"; text: string }[]>([]);
  const [introDone, setIntroDone] = useState(false);

  // Ïù∏Ìä∏Î°ú ÌÉÄÏù¥Ìïë ÏÉÅÌÉú Ï∂îÍ∞Ä
  const [typingText, setTypingText] = useState(""); // ÌòÑÏû¨ ÌÉÄÏù¥Ìïë Ï§ëÏù∏ ÌÖçÏä§Ìä∏
  // introMessagesÎ•º useMemoÎ°ú Í¥ÄÎ¶¨
  const introMessages = useMemo(() => [
    "Î∞òÍ∞ëÏäµÎãàÎã§:)",
    "ÎèôÍ≥†Î¶¨Í∞Ä Î¥âÏ†úÍ≥µÏû•ÏùÑ Ï∂îÏ≤úÌï¥ÎìúÎ¶¥Í≤åÏöî!",
    QUESTIONS[0]?.question || "Ïñ¥Îñ§ Í≥µÏ†ïÏùÑ ÏõêÌïòÏãúÎÇòÏöî?",
  ], [QUESTIONS[0]?.question]);
  const [introStep, setIntroStep] = useState(0); // 0: ÌÉÄÏù¥Ìïë, 1: ... ÌëúÏãú, 2: Îã§Ïùå Î©îÏãúÏßÄ
  const typingTimer = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    // Ïù∏Ìä∏Î°ú ÌÉÄÏù¥Ìïë Ìö®Í≥º
    setChat([]);
    setIntroDone(false);
    setTypingText("");
    setIntroStep(0);
    const timers: NodeJS.Timeout[] = [];
    let currentMsgIdx = 0;
    let currentCharIdx = 0;
    function typeNextChar() {
      const msg = introMessages[currentMsgIdx];
      if (currentCharIdx < msg.length) {
        setTypingText(msg.slice(0, currentCharIdx + 1));
        currentCharIdx++;
        typingTimer.current = setTimeout(typeNextChar, 40); // ÌÉÄÏù¥Ìïë ÏÜçÎèÑ
      } else {
        // ÌÉÄÏù¥Ìïë ÎÅùÎÇòÎ©¥ Î∞îÎ°ú Î©îÏãúÏßÄ Ï∂îÍ∞Ä Î∞è Îã§Ïùå Î©îÏãúÏßÄÎ°ú Ïù¥Îèô
        setChat(prev => [...prev, { type: "question", text: msg }]);
        setTypingText("");
        setIntroStep(2);
        if (currentMsgIdx < introMessages.length - 1) {
          currentMsgIdx++;
          currentCharIdx = 0;
          setTimeout(() => {
            setIntroStep(0);
            typeNextChar();
          }, 200); // Î∞îÎ°ú Îã§Ïùå Î©îÏãúÏßÄ ÌÉÄÏù¥Ìïë ÏãúÏûë, ÏïΩÍ∞ÑÏùò ÌÖÄÎßå Îë†
        } else {
          // Ïù∏Ìä∏Î°ú ÎÅù: setTimeout ÏóÜÏù¥ Î∞îÎ°ú introDone Ï≤òÎ¶¨
          setIntroDone(true);
        }
      }
    }
    // Ï≤´ Î©îÏãúÏßÄ ÌÉÄÏù¥Ìïë ÏãúÏûë
    typeNextChar();
    return () => {
      timers.forEach(clearTimeout);
      if (typingTimer.current) clearTimeout(typingTimer.current);
    };
  }, [introMessages]);
  // ÌòÑÏû¨ ÏßàÎ¨∏ Ïù∏Îç±Ïä§
  const [step, setStep] = useState(0);
  // ÏÇ¨Ïö©ÏûêÍ∞Ä ÏÑ†ÌÉùÌïú ÎãµÎ≥ÄÎì§
  const [answers, setAnswers] = useState<string[][]>([]);
  const [selectedOptions, setSelectedOptions] = useState<string[]>([]);
  // user Î≥ÄÏàò Ï†úÍ±∞ (ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå)
  const router = useRouter();
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const [recommended, setRecommended] = useState<ScoredFactory[]>([]);
  // Ï∂îÏ≤ú Í≤∞Í≥º Î°úÎî© ÏÉÅÌÉú Ï∂îÍ∞Ä
  const [resultLoading, setResultLoading] = useState(false);
  // Ï±ÑÌåÖ Ïä§ÌÅ¨Î°§ ref
  const chatScrollRef = useRef<HTMLDivElement>(null);

  // Îç∞Î™® Ïù¥ÎØ∏ÏßÄ Î∞∞Ïó¥(Í≥µÏû• Ï∞æÍ∏∞ÏôÄ ÎèôÏùº)
  const DEMO_IMAGES = [
    "https://images.unsplash.com/photo-1512436991641-6745cdb1723f?auto=format&fit=crop&w=600&q=80",
    "https://images.unsplash.com/photo-1465101046530-73398c7f28ca?auto=format&fit=crop&w=600&q=80",
    "https://images.unsplash.com/photo-1506744038136-46273834b3fb?auto=format&fit=crop&w=600&q=80",
    "https://images.unsplash.com/photo-1515378791036-0648a3ef77b2?auto=format&fit=crop&w=600&q=80",
    "https://images.unsplash.com/photo-1464983953574-0892a716854b?auto=format&fit=crop&w=600&q=80",
  ];
  // Ïπ¥ÎìúÎ≥Ñ Ïπ©(Í≥µÏû• id Í∏∞Ï§Ä) - Í≥µÏû• Ï∞æÍ∏∞ÏôÄ ÎèôÏùºÌïòÍ≤å Í≥†Ï†ï
  const cardFabricsById = useMemo(() => {
    const fabricChips = [
      { label: 'Î¥âÏ†ú', color: '#0ACF83', bg: 'rgba(10, 207, 131, 0.1)' },
      { label: 'ÏÉòÌîå', color: '#08B7FF', bg: 'rgba(8, 183, 255, 0.1)' },
      { label: 'Ìå®ÌÑ¥', color: '#FF8308', bg: 'rgba(255, 131, 8, 0.1)' },
      { label: 'ÎÇòÏóº', color: '#A259FF', bg: 'rgba(162, 89, 255, 0.1)' },
      { label: 'Ï†ÑÏÇ¨', color: '#ED6262', bg: 'rgba(237, 98, 98, 0.1)' },
    ];
    return Object.fromEntries(
      recommended.map((f, idx) => {
        const seed = String(f.id ?? idx);
        let hash = 0;
        for (let i = 0; i < seed.length; i++) hash = ((hash << 5) - hash) + seed.charCodeAt(i);
        const shuffled = [...fabricChips].sort((a, b) => {
          const h1 = Math.abs(Math.sin(hash + a.label.length)) % 1;
          const h2 = Math.abs(Math.sin(hash + b.label.length)) % 1;
          return h1 - h2;
        });
        const count = (Math.abs(hash) % 2) + 1;
        return [f.id ?? idx, shuffled.slice(0, count)];
      })
    );
  }, [recommended]);

  // ÏÑ†ÌÉùÏßÄ ÌÅ¥Î¶≠ Ïãú
  const handleOptionToggle = (option: string) => {
    setSelectedOptions(prev =>
      prev.includes(option)
        ? prev.filter(o => o !== option)
        : [...prev, option]
    );
  };

  // 1. showResult, loading Îì± Í≤∞Í≥º Ïò§Î≤ÑÎ†àÏù¥/Ï†ÑÏ≤¥ÌôîÎ©¥ Í¥ÄÎ†® ÏÉÅÌÉú/ÏΩîÎìú Ï†úÍ±∞
  // 2. handleConfirm, handleSkipÏóêÏÑú setShowResult, setLoading Îì± Ï†úÍ±∞, ÎåÄÏã† answersÍ∞Ä 7Í∞ú(ÎßàÏßÄÎßâ)ÍπåÏßÄ ÏåìÏù¥Î©¥ Í≤∞Í≥º ÏÉÅÌÉúÎ°ú Ï†ÑÌôò
  // 3. ÏôºÏ™Ω: 7Í∞ú ÎãµÎ≥ÄÏù¥ Î™®Îëê ÎÅùÎÇòÎ©¥ ÏßàÎ¨∏/ÏÑ†ÌÉùÏßÄ ÎåÄÏã† Ï∂îÏ≤ú 3Í∞ú Ïπ¥ÎìúÎßå ÌëúÏãú
  // 4. Ïò§Î•∏Ï™Ω: Í∏∞Ï°¥ Ï±ÑÌåÖ UI ÏïÑÎûòÏóê Í≤∞Í≥º ÏïàÎÇ¥ Î©îÏãúÏßÄ(ÎãµÎ≥Ä ÎßêÌíçÏÑ†) Ï∂îÍ∞Ä

  // handleConfirm ÎÇ¥Î∂Ä
  const handleConfirm = () => {
    if (selectedOptions.length === 0) return;
    const newAnswers = [...answers, selectedOptions];
    setAnswers(newAnswers);
    setChat(prev => [
      ...prev,
      { type: "answer", text: selectedOptions.join(", ") },
    ]);
    setSelectedOptions([]);
    // Îã§Ïùå ÏßàÎ¨∏Ïù¥ ÏûàÏúºÎ©¥ Ï∂îÍ∞Ä
    if (step + 1 < QUESTIONS.length) {
      setTimeout(() => {
        setChat(prev => [
          ...prev,
          { type: "question", text: QUESTIONS[step + 1].question },
        ]);
        setStep(prev => prev + 1);
      }, 400);
    } else {
      // 7Í∞ú ÏôÑÎ£å Ïãú Ï∂îÏ≤ú Í≤∞Í≥ºÎßå ÏÉÅÌÉúÏóê Ï†ÄÏû•
      const rec = getRecommendedFactories(newAnswers.map(a => a.join(", ")));
      setRecommended(rec);
      // step, answers Îì±ÏùÄ Í∑∏ÎåÄÎ°ú ÎëêÍ≥†, showResult/Î°úÎî© Îì±ÏùÄ ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå
    }
  };
  // handleSkipÎèÑ ÎèôÏùºÌïòÍ≤å showResult/Î°úÎî© Ï†úÍ±∞
  const handleSkip = () => {
    const newAnswers = [...answers, []];
    setAnswers(newAnswers);
    setChat(prev => [
      ...prev,
      { type: "answer", text: "(Í±¥ÎÑàÎúÄ)" },
    ]);
    setSelectedOptions([]);
    if (step + 1 < QUESTIONS.length) {
      setTimeout(() => {
        setChat(prev => [
          ...prev,
          { type: "question", text: QUESTIONS[step + 1].question },
        ]);
        setStep(prev => prev + 1);
      }, 400);
    } else {
      const rec = getRecommendedFactories(newAnswers.map(a => a.join(", ")));
      setRecommended(rec);
    }
  };

  // Ï∂îÏ≤ú ÏïåÍ≥†Î¶¨Ï¶ò: ÎãµÎ≥ÄÍ≥º Í≥µÏû• Îç∞Ïù¥ÌÑ∞ Îß§Ïπ≠ Ï†êÏàò Í≥ÑÏÇ∞
  function getRecommendedFactories(answers: string[]) {
    // ÏÑ†ÌÉùÌïú ÏòµÏÖò Ï§ë 1Í∞úÎùºÎèÑ ÏùºÏπòÌïòÎäî Í≥µÏû•Îßå ÌõÑÎ≥¥Î°ú ÏÇºÍ≥†, Í∑∏ Ï§ë ÏµúÎåÄ 3Í∞úÎßå(ÎûúÎç§ ÎòêÎäî ÏÉÅÏúÑ 3Í∞ú) ÎÖ∏Ï∂ú
    // ÏùºÏπòÌïòÎäî Í≥µÏû•Ïù¥ 3Í∞ú ÎØ∏ÎßåÏù¥Î©¥ ÎÇòÎ®∏ÏßÄÎäî ÎûúÎç§ÏúºÎ°ú Ï±ÑÏõÄ
    const matched = factories.filter(f => {
      // Í≥µÏ†ï
      if (answers[0] && Array.isArray(f.processes) && f.processes.some(p => answers[0].includes(p))) return true;
      // ÏßÄÏó≠
      if (answers[1] && typeof f.admin_district === 'string' && answers[1].includes(f.admin_district)) return true;
      // MOQ(ÏàòÎüâ)
      if (answers[2]) {
        if (
          (answers[2] === "0-50" && f.minOrder <= 50) ||
          (answers[2] === "51-100" && f.minOrder <= 100) ||
          (answers[2] === "101-300" && f.minOrder <= 300) ||
          (answers[2] === "301+" && f.minOrder > 300)
        ) return true;
      }
      // Ïû¨Î¥âÍ∏∞/Ìå®ÌÑ¥Í∏∞/ÌäπÏàòÍ∏∞
      if (answers[3] && typeof f.sewing_machines === 'string' && answers[3].split(',').some(val => typeof f.sewing_machines === 'string' && f.sewing_machines.includes(val))) return true;
      if (answers[4] && typeof f.pattern_machines === 'string' && answers[4].split(',').some(val => typeof f.pattern_machines === 'string' && f.pattern_machines.includes(val))) return true;
      if (answers[5] && typeof f.special_machines === 'string' && answers[5].split(',').some(val => typeof f.special_machines === 'string' && f.special_machines.includes(val))) return true;
      // ÌíàÎ™©
      if (answers[6] && Array.isArray(f.items) && f.items.some(i => answers[6].includes(i))) return true;
      return false;
    });
    // 3Í∞ú ÎØ∏ÎßåÏù¥Î©¥ ÎûúÎç§ÏúºÎ°ú Ï±ÑÏõÄ
    const result = matched.slice(0, 3).map(f => ({ ...f, score: 1 }));
    if (result.length < 3) {
      const others = factories.filter(f => !matched.includes(f));
      while (result.length < 3 && others.length > 0) {
        const idx = Math.floor(Math.random() * others.length);
        result.push({ ...others.splice(idx, 1)[0], score: 1 });
      }
    }
    return result;
  }

  // Ï∂îÏ≤ú Í≤∞Í≥º Ïπ¥Îìú UI (Í≥µÏû• Ï†ïÎ≥¥ ÏÉÅÏÑ∏)
  function renderResultCards() {
    return (
      <div className="w-full flex flex-col items-center justify-center min-h-[500px] animate-fade-in">
        <div className="text-2xl font-bold mb-6">Í∞ÄÏû• Ï†ÅÌï©Ìïú Î¥âÏ†úÍ≥µÏû• 3Í≥≥ÏùÑ Ï∂îÏ≤úÎìúÎ†§Ïöî!</div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8 w-full max-w-3xl">
          {recommended.map((f, idx) => {
            const displayName = typeof f.name === 'string' && f.name
              ? f.name
              : typeof f.company_name === 'string' && f.company_name
                ? f.company_name
                : 'Ïù¥Î¶Ñ ÏóÜÏùå';
            const mainItems = [f.top_items_upper, f.top_items_lower, f.top_items_outer, f.top_items_dress_skirt]
              .filter((v) => typeof v === 'string' && v.length > 0)
              .join(', ') || '-';
            const randomFabrics = cardFabricsById[f.id ?? idx] || [];
            return (
              <div key={f.id ?? idx} className="rounded-xl bg-white overflow-hidden flex flex-col border border-gray-200">
                {/* Ïù¥ÎØ∏ÏßÄ ÏòÅÏó≠ */}
                <div className="w-full h-40 bg-gray-100 flex items-center justify-center overflow-hidden rounded-xl">
                  <img
                    src={f.image || DEMO_IMAGES[idx % DEMO_IMAGES.length]}
                    alt={typeof f.company_name === 'string' ? f.company_name : 'Í≥µÏû• Ïù¥ÎØ∏ÏßÄ'}
                    className="object-cover w-full h-full rounded-xl"
                  />
                </div>
                {/* Ïù¥ÎØ∏ÏßÄÏôÄ ÌÖçÏä§Ìä∏ ÏÇ¨Ïù¥ gap */}
                <div className="mt-4" />
                {/* Ï†ïÎ≥¥ ÏòÅÏó≠ (Ìå®Îî© Ï†ÅÏö©) */}
                <div className="flex-1 flex flex-col p-6">
                  {/* Ï£ºÏöî ÏõêÎã® Ïπ© */}
                  <div className="flex flex-wrap gap-2 mb-2">
                    {randomFabrics.map((chip) => (
                      <span key={chip.label} style={{ color: chip.color, background: chip.bg }} className="rounded-full px-3 py-1 text-xs font-semibold">
                        {chip.label}
                      </span>
                    ))}
                  </div>
                  <div className="font-bold text-base mb-1">{displayName}</div>
                  {/* Ï£ºÏöî ÌíàÎ™© */}
                  <div className="text-sm font-bold mt-2 mb-1 flex items-center" style={{ color: '#333333', opacity: 0.6 }}>
                    <span className="shrink-0">Ï£ºÏöîÌíàÎ™©</span>
                    <span className="font-normal ml-2 flex-1 truncate">{mainItems}</span>
                  </div>
                  <div className="text-sm font-bold" style={{ color: '#333333', opacity: 0.6 }}>
                    MOQ(ÏµúÏÜå Ï£ºÎ¨∏ ÏàòÎüâ) <span className="font-normal">{typeof f.moq === 'number' ? f.moq : (typeof f.moq === 'string' && !isNaN(Number(f.moq)) ? Number(f.moq) : (typeof f.minOrder === 'number' ? f.minOrder : '-'))}</span>
                  </div>
                  <button
                    className="w-full mt-4 bg-[#333333] text-white rounded-lg py-2 font-semibold hover:bg-[#222] transition"
                    onClick={() => router.push(`/factories/${f.id}`)}
                  >
                    ÏùòÎ¢∞ÌïòÍ∏∞
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }

  // Î°úÎî© Ïä§ÌîºÎÑà UI
  function renderLoading() {
    return (
      <div className="w-full flex flex-col items-center justify-center min-h-[500px] animate-fade-in">
        <div className="w-16 h-16 border-4 border-gray-300 border-t-[#222222] rounded-full animate-spin mb-6"></div>
        <div className="text-lg font-semibold">Ï∂îÏ≤ú Í≤∞Í≥ºÎ•º Î∂ÑÏÑù Ï§ëÏûÖÎãàÎã§...</div>
      </div>
    );
  }

  // Ïù¥Ï†Ñ Îã®Í≥ÑÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞ (ÏàòÏ†ï)
  const handleEdit = (editStep: number) => {
    setLoading(false);
    setStep(editStep);
    setSelectedOptions(answers[editStep] || []);
    setAnswers(answers.slice(0, editStep));
    setChat(chat.slice(0, 1 + editStep * 2)); // ÏßàÎ¨∏/ÎãµÎ≥Ä ÏåçÏù¥ÎØÄÎ°ú
  };

  // Ï∂îÏ≤ú Í≤∞Í≥º(Îß§Ïπ≠ ÏôÑÎ£å) Ïãú ÏôºÏ™Ω ÌïòÎã®Ïóê 'ÏßÅÏ†ë Ï∞æÍ∏∞'ÏôÄ 'Îã§ÏãúÌïòÍ∏∞' Î≤ÑÌäºÏùÑ Ï∂îÍ∞ÄÌï©ÎãàÎã§. 'ÏßÅÏ†ë Ï∞æÍ∏∞'Îäî /factoriesÎ°ú Ïù¥Îèô, 'Îã§ÏãúÌïòÍ∏∞'Îäî Îß§Ïπ≠ ÏÉÅÌÉú(answers, step, chat Îì±) Ï¥àÍ∏∞Ìôî. Î≤ÑÌäºÏùÄ Figma ÏòàÏãúÏ≤òÎüº Ïä§ÌÉÄÏùºÎßÅ(ÏßÅÏ†ë Ï∞æÍ∏∞: Ìù∞ÏÉâ, Îã§ÏãúÌïòÍ∏∞: Í≤ÄÏ†ï Î∞∞Í≤Ω, ÏïÑÏù¥ÏΩò Ìè¨Ìï®)Ìï©ÎãàÎã§.
  const handleRestart = () => {
    setLoading(false);
    setStep(0);
    setAnswers([]);
    setSelectedOptions([]);
    setChat([]);
    setIntroDone(false);
    setRecommended([]); // Í≤∞Í≥º Ï¥àÍ∏∞Ìôî
  };

  // ÎãµÎ≥ÄÏù¥ Î™®Îëê ÎÅùÎÇòÎ©¥ Î∂ÑÏÑù Î°úÎî© ÌõÑ Í≤∞Í≥º ÎÖ∏Ï∂ú
  useEffect(() => {
    if (answers.length === QUESTIONS.length) {
      setResultLoading(true);
      const timer = setTimeout(() => {
        const rec = getRecommendedFactories(answers.map(a => a.join(", ")));
        setRecommended(rec);
        setResultLoading(false);
      }, 2200); // 2.2Ï¥à Î∂ÑÏÑù Î°úÎî©
      return () => clearTimeout(timer);
    } else {
      setResultLoading(false);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [answers.length]);

  // Ï±ÑÌåÖ ÏûêÎèô Ïä§ÌÅ¨Î°§ useEffect
  useEffect(() => {
    if (chatScrollRef.current) {
      chatScrollRef.current.scrollTop = chatScrollRef.current.scrollHeight;
    }
  }, [chat, recommended, resultLoading]);

  // --- [Ï∂îÍ∞Ä: Í≤∞Í≥º ÏïàÎÇ¥ Î©îÏãúÏßÄ ÏÉÅÌÉú] ---
  const [showResultMsg1, setShowResultMsg1] = useState(false);
  const [showResultMsg2, setShowResultMsg2] = useState(false);

  // --- [Í≤∞Í≥º ÏïàÎÇ¥ Î©îÏãúÏßÄ ÌÉÄÏù¥Î∞ç Ï†úÏñ¥ useEffect ÏàòÏ†ï] ---
  useEffect(() => {
    if (
      answers.length === QUESTIONS.length &&
      recommended.length > 0 &&
      !resultLoading
    ) {
      setShowResultMsg1(false);
      setShowResultMsg2(false);
      const t1 = setTimeout(() => setShowResultMsg1(true), 500); // 0.5Ï¥à ÌõÑ Ï≤´ Î©îÏãúÏßÄ
      const t2 = setTimeout(() => setShowResultMsg2(true), 1500); // 1.5Ï¥à ÌõÑ Îëê Î≤àÏß∏ Î©îÏãúÏßÄ
      return () => {
        clearTimeout(t1);
        clearTimeout(t2);
      };
    } else {
      setShowResultMsg1(false);
      setShowResultMsg2(false);
    }
  }, [answers.length, recommended, resultLoading]);

  // Í≤∞Í≥º ÏïàÎÇ¥ Î©îÏãúÏßÄ Îì±Ïû• Ïãú Ï±ÑÌåÖÏ∞Ω ÏûêÎèô Ïä§ÌÅ¨Î°§
  useEffect(() => {
    if ((showResultMsg1 || showResultMsg2) && chatScrollRef.current) {
      chatScrollRef.current.scrollTop = chatScrollRef.current.scrollHeight;
    }
  }, [showResultMsg1, showResultMsg2]);

  // ÏôºÏ™Ω: ÏßàÎ¨∏/ÏÑ†ÌÉùÏßÄ or Í≤∞Í≥º Ïπ¥Îìú or Î°úÎî©
  return (
    <div className="w-full min-h-screen bg-[#F4F5F7] flex flex-col items-center justify-start overflow-x-hidden py-16 px-6">
      <div className="w-full max-w-[1400px] mx-auto flex flex-row gap-4 items-start justify-center flex-1 transition-opacity duration-700 px-1 overflow-hidden bg-[#F4F5F7] pb-0 mb-0" style={{ minHeight: '84vh' }}>
        {/* ÏôºÏ™Ω: ÏßàÎ¨∏/ÏÑ†ÌÉùÏßÄ or Í≤∞Í≥º Ïπ¥Îìú or Î°úÎî© */}
        <div className="flex-[2] bg-white rounded-2xl shadow border p-6 flex flex-col h-[100vh] max-h-[80vh]">
          {answers.length === QUESTIONS.length ? (
            resultLoading ? (
              <div className="flex flex-1 flex-col items-center justify-center min-h-[400px] animate-fade-in">
                <div className="w-16 h-16 border-4 border-gray-300 border-t-[#222222] rounded-full animate-spin mb-6"></div>
                <div className="text-lg font-semibold">Ï∂îÏ≤ú Í≤∞Í≥ºÎ•º Î∂ÑÏÑù Ï§ëÏûÖÎãàÎã§...</div>
              </div>
            ) : (
              <div className="flex flex-col flex-1 justify-between h-full">
                <div className="flex-1 flex flex-col items-center justify-center">
                  {renderResultCards()}
                </div>
                {/* ÌïòÎã® Î≤ÑÌäº ÏòÅÏó≠ */}
                <div className="flex w-full gap-4 pt-4">
                  <button
                    className="flex-1 flex items-center justify-center border border-gray-300 rounded-lg py-3 text-base font-semibold bg-white hover:bg-gray-50 transition"
                    onClick={() => router.push('/factories')}
                  >
                    <span className="mr-2">üîç</span>ÏßÅÏ†ë Ï∞æÍ∏∞
                  </button>
                  <button
                    className="flex-1 flex items-center justify-center rounded-lg py-3 text-base font-semibold bg-[#222] text-white hover:bg-[#111] transition"
                    onClick={handleRestart}
                  >
                    <span className="mr-2">‚Üª</span>Îã§ÏãúÌïòÍ∏∞
                  </button>
                </div>
              </div>
            )
          ) : !introDone ? (
            // Ïù∏Ìä∏Î°ú ÌÉÄÏù¥Ìïë Ï§ëÏóêÎäî ÏïÑÎ¨¥Í≤ÉÎèÑ Ïïà Î≥¥Ïù¥Í≤å(ÎòêÎäî Î°úÎî©/Ïä§ÏºàÎ†àÌÜ§ Îì±)
            <div className="flex-1 flex items-center justify-center text-gray-400 text-lg">...</div>
          ) : (
            // Í∏∞Ï°¥ ÏßàÎ¨∏/ÏÑ†ÌÉùÏßÄ UI
            <>
              {/* ÏÉÅÎã®~ÏÑ†ÌÉùÏßÄ ÏòÅÏó≠ */}
              <div className="flex-1 min-h-0 flex flex-col gap-4">
                <div className="text-base text-gray-500 mb-4">
                  Î™á Í∞ÄÏßÄ Ï†ïÎ≥¥Î•º ÏïåÎ†§Ï£ºÏãúÎ©¥,<br />
                  <span className="font-bold">Í∞ÄÏû• Ï†ÅÌï©Ìïú 3Í∞úÏùò Î¥âÏ†úÍ≥µÏû•ÏùÑ Ï∂îÏ≤ú</span>Ìï¥ÎìúÎ¶ΩÎãàÎã§.
                </div>
                <hr className="my-4 border-gray-200" />
                <div className="flex gap-2 mb-6">
                  {QUESTIONS.map((_, idx) => (
                    <div key={idx} className={`h-1 w-12 rounded-full ${idx <= step ? "bg-[#333333]" : "bg-gray-200"}`}></div>
                  ))}
                </div>
                <div className="text-sm text-gray-400 mb-2">{step + 1} of {QUESTIONS.length}</div>
                <div className="text-xl font-bold mb-6">{QUESTIONS[step].question}</div>
                {/* ÏÑ†ÌÉùÏßÄ ÏòÅÏó≠ */}
                <div className={QUESTIONS[step].key === 'items' ? 'bg-gray-50 rounded-xl p-6 grid grid-cols-3 gap-6 overflow-y-auto flex-1' : 'bg-gray-50 rounded-xl p-6 grid grid-cols-3 gap-6 overflow-y-auto flex-1'}
                  style={QUESTIONS[step].key === 'items' ? { maxHeight: 'unset' } : {}}>
                  {QUESTIONS[step].options.map(option => (
                    <button
                      key={option}
                      type="button"
                      className={`rounded-xl bg-white shadow text-[15px] font-medium py-8 transition border border-gray-200 flex items-center justify-center
                        ${selectedOptions.includes(option)
                          ? "border-[#333333] ring-2 ring-[#333333]"
                          : "hover:border-[#333333]"}
                      `}
                      onClick={() => handleOptionToggle(option)}
                    >
                      {option}
                    </button>
                  ))}
                </div>
                <div className="h-6" />
              </div>
              {/* ÌïòÎã® Î≤ÑÌäº ÏòÅÏó≠ */}
              <div className="pt-4 pb-6 border-t border-gray-200 flex justify-between items-center gap-4 shrink-0 bg-white">
                <button className="text-base text-gray-500 underline" onClick={() => router.push("/")}>ÎÇòÍ∞ÄÍ∏∞</button>
                <div className="flex gap-2">
                  <Button variant="ghost" className="text-[#333333] text-base px-6 py-3" onClick={handleSkip}>Í±¥ÎÑàÎõ∞Í∏∞</Button>
                  <Button
                    className="bg-[#333333] text-white rounded-lg px-8 py-3 font-bold text-base"
                    onClick={handleConfirm}
                    disabled={selectedOptions.length === 0}
                  >
                    Îã§Ïùå
                  </Button>
                </div>
              </div>
            </>
          )}
        </div>
        {/* Ïò§Î•∏Ï™Ω: Í∏∞Ï°¥ Ï±ÑÌåÖ UI + Í≤∞Í≥º ÏïàÎÇ¥ Î©îÏãúÏßÄ(ÎãµÎ≥Ä ÎßêÌíçÏÑ†) */}
        <div
          className="flex-[1] bg-[#F7F8FA] rounded-xl shadow-md p-4 min-h-[500px] flex flex-col gap-6"
          style={{ height: '80vh', maxHeight: '80vh', overflowY: 'auto' }}
          ref={chatScrollRef}
        >
          {/* Í∏∞Ï°¥ Ï±ÑÌåÖ/Ïù∏Ìä∏Î°ú UI */}
          {chat.map((msg, idx) => (
            <div key={idx} className={`flex ${msg.type === "answer" ? "justify-end" : "justify-start"}`}>
              <ChatBubble
                text={msg.text}
                type={msg.type}
                onEdit={msg.type === "answer" && introDone ? () => handleEdit(Math.floor((idx - introMessages.length) / 2)) : undefined}
              />
            </div>
          ))}
          {/* ÌòÑÏû¨ ÌÉÄÏù¥Ìïë Ï§ëÏù∏ Î©îÏãúÏßÄ */}
          {!introDone && typingText && (
            <div className="flex justify-start">
              <ChatBubble text={typingText} type="question" isTyping={true} showCursor={introStep === 0} />
            </div>
          )}
          {/* Í≤∞Í≥º ÏïàÎÇ¥ Î©îÏãúÏßÄ(ÎãµÎ≥Ä ÎßêÌíçÏÑ†) - Îëê Í∞úÎ°ú Î∂ÑÎ¶¨, ÏàúÏ∞® Îì±Ïû• */}
          {showResultMsg1 && recommended.length > 0 && (
            <div className="flex justify-end">
              <ChatBubble
                text={`Í∞ÄÏû• Ï†ÅÌï©Ìïú Î¥âÏ†úÍ≥µÏû•ÏùÄ\n${recommended.map(f => (typeof f.name === 'string' && f.name) ? f.name : (typeof f.company_name === 'string' && f.company_name) ? f.company_name : 'Ïù¥Î¶Ñ ÏóÜÏùå').join(', ')} ÏûÖÎãàÎã§!`}
                type="answer"
              />
            </div>
          )}
          {showResultMsg2 && (
            <div className="flex justify-end">
              <ChatBubble
                text={`Î¥âÏ†úÎ•º ÏßÑÌñâÌï† Í≥µÏû•ÏùÑ ÏÑ†ÌÉùÌïòÏó¨\nÍ≥µÏ†ïÏùÑ ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî:)`}
                type="answer"
              />
            </div>
          )}
        </div>
      </div>
      {/* Î°úÍ∑∏Ïù∏ ÌïÑÏöî Î™®Îã¨ */}
      {showLoginModal && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-40 z-50">
          <div className="bg-white rounded-xl shadow-lg p-8 max-w-xs w-full text-center">
            <div className="text-lg font-bold mb-2">Î°úÍ∑∏Ïù∏ ÌõÑ Ïù¥Ïö© Í∞ÄÎä•Ìï©ÎãàÎã§</div>
            <div className="text-gray-500 mb-4">ÏùòÎ¢∞ÌïòÍ∏∞Îäî Î°úÍ∑∏Ïù∏ ÌõÑ Ïù¥Ïö©ÌïòÏã§ Ïàò ÏûàÏäµÎãàÎã§.</div>
            <Button className="w-full mb-2" onClick={() => router.push("/sign-in")}>Î°úÍ∑∏Ïù∏ ÌôîÎ©¥ÏúºÎ°ú Ïù¥Îèô</Button>
            <Button variant="outline" className="w-full" onClick={() => setShowLoginModal(false)}>Îã´Í∏∞</Button>
          </div>
        </div>
      )}
      {/* Î°úÎî© ÌôîÎ©¥ */}
      {loading && (
        <div className="absolute inset-0 flex items-center justify-center bg-gray-50 z-50 transition-opacity duration-700 animate-fade-in">
          {renderLoading()}
        </div>
      )}
      {/* Ï∂îÏ≤ú Í≤∞Í≥º ÌôîÎ©¥ */}
      {/* showResult && ( */}
      {/*   <div className="absolute inset-0 flex items-center justify-center bg-gray-50 z-50 transition-opacity duration-700 animate-fade-in"> */}
      {/*     {renderResultCards()} */}
      {/*   </div> */}
      {/* ) */}
      {/* fade-in, blink Ïï†ÎãàÎ©îÏù¥ÏÖò ÏßÅÏ†ë Ï∂îÍ∞Ä (TailwindÏóê ÏóÜÏùÑ Ïàò ÏûàÏùå) */}
      {/* return Î¨∏ ÎßàÏßÄÎßâÏóê Ï∂îÍ∞Ä */}
      <style jsx global>{`
@keyframes fade-in {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: none; }
}
.animate-fade-in {
  animation: fade-in 0.5s cubic-bezier(0.4,0,0.2,1);
}
@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}
.animate-blink {
  animation: blink 1s step-end infinite;
}
`}</style>
    </div>
  );
}